*{
    padding: 0;
    margin: 0;
    font-family: 'Merriweather Sans', sans-serif;
}

.sm{
    display: none;
}

header.lg-xl{
    nav{
        background-color: #CFD984;
        display: grid;
        width: 100%;
        height: 5vh;
        grid-template-columns: repeat(5, 1fr);
        font-weight: bold;
        grid-template-areas:  
        'logo link link link tema'
        ;

        > a{
            text-decoration: none;
            align-self: center;
            grid-area: logo;
            margin: 1vh;
        }

        .toggle{
            grid-area: tema;
            justify-self: end;
        }

        ul{
            grid-area: link;
            justify-self: center;
            align-self: center;
            display: inline-block;
            list-style: none;
            li{
                display: inline-block;
                a{
                    text-decoration: none;
                    padding: 2vh;
                    
                    &:visited{
                        color: black;
                    }

                    &:hover{
                        color: #3C2440;
                        transition: 400ms;
                        font-size: 2em;
                    }

                    &:link {
                        color: #FF0000;
                    }
                    
                    &:active{
                        transition: 0ms;
                        color: #FF0000;
                    }
                }
            }
        }
    }
}

// 1100 primeira troca screen






@media screen and (max-width: 992px) {
    body {
      background-color: blue;
    }
  }
  
/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 760px) {
    body {
        background-color: olive;
    }
    .sm{
        display:inline;

        nav{
            background-color: #CFD984;
            display: grid;
            width: 100%;
            min-height: 5vh;
            grid-template-columns: repeat(3, 1fr);
            font-weight: bold;
            grid-template-areas:  
            'logo tema link'
            ;
    
            > a{
                text-decoration: none;
                align-self: center;
                grid-area: logo;
                margin: 1vh;
            }
            
            .toggle{
                grid-area: tema;
                justify-self: center;
                align-self: center;
            }
            
            #select{
                grid-area: link;
                justify-self: end;
                align-self: center;
                margin-right: 1vh;
            }
        }
    }

    .lg-xl{
        display: none;
    }

}

@media screen and (max-width: 640px) {
    body{
        background-color: teal;
    }
}


